app/projects/[projectId]/page.js
"use client";

import React, { useState, useEffect } from "react";
import { useParams, useRouter } from "next/navigation";
import { format } from "date-fns";
import {
  Plus,
  Edit,
  Trash2,
  X,
  Maximize2,
  ArrowLeft,
  ChevronRight,
  User,
  Calendar,
  Bug,
  FileText,
  Home,
} from "lucide-react";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";

export default function ProjectPage() {
  const router = useRouter();
  const { projectId, slug = [] } = useParams();
  const slugArray = Array.isArray(slug) ? slug : [slug];
  const module = slugArray[0] || "overview";
  const itemId = slugArray[1] ? Number(slugArray[1]) : null;
  const action = slugArray[2]; // create | edit

  // Dummy data
  const [tasks, setTasks] = useState([
    { id: 1, title: "Redesign homepage", description: "Modern hero with animations", status: "In Progress", assignee: "Sarah", dueDate: "2025-12-10" },
    { id: 2, title: "Add user roles", description: "Admin, editor, viewer", status: "Open", assignee: "Mike", dueDate: null },
  ]);
  const [bugs] = useState([
    { id: 101, title: "Login fails on Safari", severity: "High", status: "Open", assignedTo: "Carol" },
  ]);
  const [reports] = useState([
    { id: 201, name: "November Report", type: "monthly", createdBy: "You" },
  ]);

  // Panel state
  const [selectedItem, setSelectedItem] = useState(null);
  const [isExpanded, setIsExpanded] = useState(false);
  const [panelMode, setPanelMode] = useState("view"); // view | create | edit

  // Sync URL with panel
  useEffect(() => {
    if (action === "create") {
      setPanelMode("create");
      setSelectedItem({ module });
      setIsExpanded(false);
    } else if (action === "edit" && itemId) {
      setPanelMode("edit");
      const item = getItem(module, itemId);
      setSelectedItem(item ? { ...item, module } : null);
    } else if (itemId) {
      setPanelMode("view");
      const item = getItem(module, itemId);
      setSelectedItem(item ? { ...item, module } : null);
    } else {
      setSelectedItem(null);
    }
  }, [module, itemId, action]);

  const getItem = (mod, id) => {
    if (mod === "task") return tasks.find(t => t.id === id);
    if (mod === "bug") return bugs.find(b => b.id === id);
    if (mod === "report") return reports.find(r => r.id === id);
    return null;
  };

  const closePanel = () => {
    router.push(`/projects/${projectId}/${module}`);
    setSelectedItem(null);
    setIsExpanded(false);
  };

  const saveItem = (formData) => {
    const data = Object.fromEntries(formData);
    if (panelMode === "create" && module === "task") {
      const newId = Math.max(...tasks.map(t => t.id), 0) + 1;
      setTasks(prev => [{ id: newId, ...data }, ...prev]);
      router.push(`/projects/${projectId}/task/${newId}`);
    } else if (panelMode === "edit" && selectedItem) {
      setTasks(prev => prev.map(t => t.id === selectedItem.id ? { ...t, ...data } : t));
      router.push(`/projects/${projectId}/task/${selectedItem.id}`);
    }
  };

  return (
    <div className="flex h-screen bg-gray-50">
      {/* LEFT SIDEBAR - Fixed List */}
      <div className={`transition-all duration-500 ease-in-out ${isExpanded ? "w-0 opacity-0 overflow-hidden" : "w-96"} bg-white border-r border-gray-200 flex flex-col`}>
        <div className="p-6 border-b">
          <h1 className="text-2xl font-bold">Project {projectId}</h1>
          <p className="text-sm text-gray-500">Team Dashboard</p>
        </div>

        <Tabs value={module} onValueChange={(v) => router.push(`/projects/${projectId}/${v}`)} className="flex-1 flex flex-col">
          <TabsList className="grid grid-cols-4 w-full rounded-none border-b">
            <TabsTrigger value="overview"><Home className="w-4 h-4 mr-2" />Overview</TabsTrigger>
            <TabsTrigger value="task">Tasks</TabsTrigger>
            <TabsTrigger value="bug"><Bug className="w-4 h-4 mr-2" />Bugs</TabsTrigger>
            <TabsTrigger value="report"><FileText className="w-4 h-4 mr-2" />Reports</TabsTrigger>
          </TabsList>

          <div className="flex-1 overflow-y-auto p-4 space-y-3">
            {module === "task" && tasks.map(task => (
              <Card
                key={task.id}
                className={`p-4 cursor-pointer transition-all hover:shadow-md ${selectedItem?.id === task.id ? "border-blue-500 border-2" : ""}`}
                onClick={() => router.push(`/projects/${projectId}/task/${task.id}`)}
              >
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <h3 className="font-semibold">{task.title}</h3>
                    <div className="flex items-center gap-3 mt-2 text-xs text-gray-500">
                      <Badge variant={task.status === "Open" ? "secondary" : "default"}>{task.status}</Badge>
                      <span className="flex items-center gap-1"><User className="w-3.5 h-3.5" />{task.assignee}</span>
                    </div>
                  </div>
                  <ChevronRight className="w-5 h-5 text-gray-400" />
                </div>
              </Card>
            ))}
          </div>
        </Tabs>

        {module === "task" && (
          <div className="p-4 border-t">
            <Button className="w-full" onClick={() => router.push(`/projects/${projectId}/task/create`)}>
              <Plus className="w-4 h-4 mr-2" /> New Task
            </Button>
          </div>
        )}
      </div>

      {/* RIGHT PANEL - Sliding + Expandable (2-column feel when normal) */}
      <div
        className={`transition-all duration-500 ease-in-out bg-white flex-1 ${
          selectedItem
            ? isExpanded
              ? "w-full"                                   // Full screen
              : "w-full max-w-4xl mx-auto my-8 shadow-2xl rounded-lg"  // 2-column look
            : "w-0"
        } overflow-hidden`}
      >
        {selectedItem && (
          <>
            {/* Header */}
            <div className="sticky top-0 bg-white border-b z-10 px-8 py-6 flex items-center justify-between">
              <h1 className="text-2xl font-bold pr-10">
                {panelMode === "create" && "New Task"}
                {panelMode === "edit" && `Edit: ${selectedItem.title}`}
                {panelMode === "view" && selectedItem.title}
              </h1>
              <div className="flex items-center gap-2">
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={() => setIsExpanded(!isExpanded)}
                  title={isExpanded ? "Collapse" : "Expand"}
                >
                  {isExpanded ? <ArrowLeft className="w-5 h-5" /> : <Maximize2 className="w-5 h-5" />}
                </Button>
                <Button variant="ghost" size="icon" onClick={closePanel}>
                  <X className="w-5 h-5" />
                </Button>
              </div>
            </div>

            {/* Content */}
            <div className="p-8 space-y-10 overflow-y-auto h-full">
              {/* VIEW MODE */}
              {panelMode === "view" && selectedItem.module === "task" && (
                <>
                  <div className="grid grid-cols-2 gap-10">
                    <div>
                      <Label className="flex items-center gap-2 text-gray-600"><User className="w-4 h-4" /> Assignee</Label>
                      <p className="text-xl font-medium mt-2">{selectedItem.assignee}</p>
                    </div>
                    <div>
                      <Label className="flex items-center gap-2 text-gray-600"><Calendar className="w-4 h-4" /> Due Date</Label>
                      <p className="text-xl font-medium mt-2">
                        {selectedItem.dueDate ? format(new Date(selectedItem.dueDate), "MMMM d, yyyy") : "No due date"}
                      </p>
                    </div>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold mb-4">Description</h3>
                    <p className="text-gray-700 leading-relaxed text-lg">{selectedItem.description}</p>
                  </div>

                  <div className="flex gap-3">
                    <Button onClick={() => router.push(`/projects/${projectId}/task/${selectedItem.id}/edit`)}>
                      <Edit className="w-4 h-4 mr-2" /> Edit Task
                    </Button>
                    <Button variant="destructive">
                      <Trash2 className="w-4 h-4 mr-2" /> Delete
                    </Button>
                  </div>
                </>
              )}

              {/* CREATE / EDIT FORM */}
              {(panelMode === "create" || panelMode === "edit") && selectedItem.module === "task" && (
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    saveItem(new FormData(e.target));
                  }}
                  className="space-y-8"
                >
                  <div>
                    <Label>Title</Label>
                    <Input name="title" defaultValue={selectedItem.title || ""} required className="mt-2" />
                  </div>
                  <div>
                    <Label>Description</Label>
                    <Textarea name="description" defaultValue={selectedItem.description || ""} className="min-h-48 mt-2" />
                  </div>
                  <div className="grid grid-cols-2 gap-6">
                    <div>
                      <Label>Status</Label>
                      <Select name="status" defaultValue={selectedItem.status || "Open"}>
                        <SelectTrigger className="mt-2"><SelectValue /></SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Open">Open</SelectItem>
                          <SelectItem value="In Progress">In Progress</SelectItem>
                          <SelectItem value="Done">Done</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label>Assignee</Label>
                      <Input name="assignee" defaultValue={selectedItem.assignee || ""} className="mt-2" />
                    </div>
                  </div>
                  <div className="flex justify-end gap-3 pt-8">
                    <Button type="button" variant="outline" onClick={closePanel}>Cancel</Button>
                    <Button type="submit">Save Task</Button>
                  </div>
                </form>
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
}




